html {
  font-size: 62.5%;
  box-sizing: border-box;
}

*, *:before, *:after {
  box-sizing: inherit;
}

body {
  font-family: "Epilogue", sans-serif;
  font-size: 1.6rem;
  font-weight: 300;
}

.container {
  width: 95%;
  max-width: 1200px;
  margin: 0 auto;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  padding: 0;
  list-style: none;
  margin: 0;
}

h1, h2, h3 {
  margin: 0;
  line-height: 1.2;
}

h1, h2 {
  font-weight: 400;
}

h1 {
  font-size: 4.4rem;
}

h2 {
  font-size: 3.4rem;
}

h3 {
  font-size: 2.4rem;
  font-weight: 300;
}

[class$=heading] {
  margin: 2rem 0;
}

a {
  text-decoration: none;
  color: #000;
}

p {
  font-size: 1.6rem;
  line-height: 1.8;
}

@media (min-width: 768px) {
  .main__grid {
    display: flex;
    flex-direction: row-reverse;
    align-items: center;
    padding: 10rem;
    gap: 8rem;
  }
}
.main__article {
  padding: 5.6rem 0 0 0;
  text-align: center;
}
@media (min-width: 768px) {
  .main__article {
    text-align: left;
    padding: 0;
  }
}
.main__title {
  font-size: 3.2rem;
  font-weight: 700;
}
@media (min-width: 768px) {
  .main__title {
    font-size: 5.2rem;
  }
}
.main__paragraph {
  line-height: 1.5;
  margin: 1.5rem 0;
}
@media (min-width: 768px) {
  .main__paragraph {
    margin: 2rem 0;
  }
}
.main__cta {
  background-color: hsl(0deg, 0%, 8%);
  color: #fff;
  display: inline-block;
  padding: 0.8rem 1.5rem;
  border-radius: 1rem;
  text-decoration: none;
  font-weight: 700;
}
@media (min-width: 768px) {
  .main__cta {
    margin-bottom: 3rem;
  }
}
.main__img {
  width: 100%;
}
@media (min-width: 768px) {
  .main__img {
    height: 100%;
  }
}
.main__brands {
  display: grid;
  gap: 1rem;
  grid-auto-flow: column;
  grid-auto-columns: minmax(30px, 100px);
  align-items: center;
  justify-content: space-between;
  margin: 4rem 0 8rem;
}
@media (min-width: 768px) {
  .main__brands {
    margin: auto 0 0 0;
  }
}
.main__brand {
  width: 100%;
}
@media (min-width: 768px) {
  .main__texts {
    margin: auto;
  }
}
.main__picBox {
  width: 100%;
}

.container {
  overflow: hidden;
}

.nav {
  height: 7rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
@media (min-width: 768px) {
  .nav {
    gap: 2rem;
    overflow: visible;
    padding: 5rem 0 0;
  }
}
.nav__hamburguer {
  width: 3rem;
  height: 50%;
  background-image: url(../../../../build/img/icon-menu.svg);
  background-repeat: no-repeat;
  background-position: center;
  cursor: pointer;
  z-index: 1;
  transition: background-image 0.3s;
}
@media (min-width: 768px) {
  .nav__hamburguer {
    display: none;
  }
}
.nav__hamburguer--open {
  background-image: url(../../../../build/img/icon-close-menu.svg);
}
.nav__overlay {
  background-color: rgba(0, 0, 0, 0.592);
  position: fixed;
  inset: 0;
  opacity: 0;
  pointer-events: none;
  transition: 0.3s opacity;
}
@media (min-width: 768px) {
  .nav__overlay {
    position: unset;
    background-color: transparent;
    opacity: 1;
    pointer-events: unset;
    flex: 1;
  }
}
.nav__overlay--show {
  opacity: 1;
  pointer-events: unset;
}
.nav__menu {
  position: absolute;
  top: 0;
  bottom: 0;
  right: 0;
  background-color: #fff;
  width: 65%;
  max-width: 40rem;
  padding: 0;
  overflow: auto;
  padding: 7rem 0;
}
@media (min-width: 768px) {
  .nav__menu {
    position: unset;
    width: 100%;
    max-width: unset;
    background-color: unset;
    padding: 0;
    overflow: unset;
    display: flex;
    align-items: center;
    gap: 1rem;
  }
}
.nav__item {
  width: 80%;
  margin: 0 auto;
  color: hsl(0deg, 0%, 8%);
  cursor: pointer;
  padding: 2rem 0;
}
.nav__item:has(.nav__parent) {
  padding: 0;
}
@media (min-width: 768px) {
  .nav__item {
    width: max-content;
    margin: 0;
    padding: 0;
    position: relative;
  }
}
.nav__inner {
  padding: 0;
  width: 80%;
  margin: 0 auto;
  display: grid;
  gap: 2rem;
  overflow: hidden;
  height: 0;
  transition: height 0.4s;
}
@media (min-width: 768px) {
  .nav__inner {
    position: absolute;
    left: 0;
    width: max-content;
    padding: 2rem;
    height: auto;
    padding: 2rem;
    background-color: #fff;
    z-index: 100;
    border-radius: 1rem;
    box-shadow: 0 0 10px 0 rgba(0, 0, 0, 0.5);
    transition: opacity 0.3s;
    opacity: 0;
    pointer-events: none;
  }
}
.nav__inner--show {
  opacity: 1;
  pointer-events: unset;
}
.nav__parent {
  display: grid;
  align-items: center;
  grid-auto-flow: column;
  grid-auto-columns: max-content;
  gap: 2rem;
  padding: 2rem 0;
}
.nav__link {
  color: hsl(0deg, 0%, 8%);
}
.nav__ilustracion {
  display: inline-block;
  margin-right: 0.5rem;
}
.nav__login {
  width: 80%;
  margin: 0 auto;
  text-align: center;
}
@media (min-width: 768px) {
  .nav__login {
    width: max-content;
    margin: 0;
    margin-left: auto;
  }
}
.nav__login--border {
  border-radius: 1rem;
  margin-top: 2rem;
  border: 1px solid hsl(0deg, 0%, 41%);
}
@media (min-width: 768px) {
  .nav__login--border {
    margin: 0;
    padding: 0 2.5rem;
  }
}
.nav__sign {
  display: inline-block;
  color: hsl(0deg, 0%, 41%);
  padding: 1rem 0;
}